@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
.body {
  font-family: 'Quicksand', sans-serif;
  text-align: center;
  margin-top: 2px;

  display: grid;
  grid-template-areas:
  "all all all"
  "def det det"
  "fav1 fav2 fav3";
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 2fr 12fr 12fr;
  grid-gap: 30px;
}

.header {
  grid-area: all;
  text-align: left;
  margin-left: 10px;
}

.def {
  grid-area: def;
  font-size: 40px;
}
.default {
  display: grid;
  grid-template-areas:
  "ct ct"
  "img tmp";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.5fr 0.5fr;
}

.city {
  grid-area: ct;
}

.temp {
  grid-area: tmp;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.image {
  grid-area: img;
  display: flex;
  justify-content: center;
}

.image img{
  height: 126px;
}

