.details {
  grid-area: det;
  font-size: 20px;
}

#list {
  width: 87%;
  display: grid;
  grid-template-areas:
  "ct tmp img img crossButton"
  "ct tmp img img crossButton"
  "wind wind wind wind wind"
  "overcast overcast overcast overcast overcast"
  "pressure pressure pressure pressure pressure"
  "humidity humidity humidity humidity humidity"
  "coordinates coordinates coordinates coordinates coordinates";
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 0.25fr 0.25fr 0.25fr 0.25fr;
}

.favCity {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 15%;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
#city1 {
  grid-area: fav1;
}
#city2 {
  grid-area: fav2;
}
#city3 {
  grid-area: fav3;
}
#addButton {
  cursor: pointer;
}

.city {
  grid-area: ct;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 30px;
}

.temp {
  grid-area: tmp;
  font-size: 30px;
}

.image {
  grid-area: img;
}

.wind {
  grid-area: wind;
}

.overcast {
  grid-area: overcast;
}

.pressure {
  grid-area: pressure;
}

.humidity {
  grid-area: humidity;
}

.coordinates {
  grid-area: coordinates;
}

.crossButton {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-area: crossButton;
}

body {
  background-image: url(/weather-react/static/media/backImg.ff74bc44.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

